import React from "react";

import Plyr from "plyr-react";
import "plyr-react/dist/plyr.css";

const Player = ({ url }) => {
  const videoSrc = {
    type: "video",
    poster: " ",
    title: "Example title",
    sources: [
      {
        src: url,
        size: 1080,
        provider: "youtube",
      },
    ],
  };
  return (
    <>
      <Plyr source={videoSrc} />{" "}
    </>
  );
};

export default Player;
