import React from "react";
import { makeStyles, useTheme } from "@material-ui/core/styles";

import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import LanguageIcon from "@material-ui/icons/Language";
import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";
import PlayCircleOutlineIcon from "@material-ui/icons/PlayCircleOutline";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import TouchAppIcon from "@material-ui/icons/TouchApp";
import {
  Card,
  CardMedia,
  Dialog,
  Grid,
  Grow,
  IconButton,
} from "@material-ui/core";
import Player from "../General/Player";

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const tutorialSteps = [
  {
    // label: "ЦАХИМ СУРГАЛТАД ТУЛГАМДАЖ БУЙ АСУУДАЛ, ШИЙДЭЛ",
    imgPath: "http://clouduni.mn:3000/upload/programfiles/Cover.JPG",
    link: "",
    btnName: "Танилцуулга",
  },
  {
    label: "2021-2022 оны хичээлийн жилийн бакалаврын хөтөлбөрийн элсэлт ",
    label2: "БҮРТГЭЛ: 2021.05.27 - 2021.07.06 -НЫ ӨДӨРҮҮД",
    imgPath: "http://clouduni.mn:3000/upload/programfiles/logo3.jpg",
    link: "",
    btnName: "www.elselt.khu.edu.mn элсэлтийн вэб хуудасаар дамжуулан ХИЙНЭ.",
  },
];

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: "100%",
    flexGrow: 1,
  },

  header: {
    display: "flex",
    alignItems: "center",
    height: 50,
    paddingLeft: theme.spacing(4),
    backgroundColor: theme.palette.background.default,
  },
  media: {
    // width: "100%",
    // margin: 0,
    // opacity: 0.7,
    height: "60vh",
    textAlign: "center",
  },
}));

const MainSlide = () => {
  const classes = useStyles();
  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = tutorialSteps.length;
  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step) => {
    setActiveStep(step);
  };
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div className={classes.root}>
      <Dialog
        fullWidth
        maxWidth="md"
        open={open}
        onClose={handleClose}
        aria-labelledby="form-dialog-title"
      >
        <Player url="https://www.youtube.com/watch?v=9a61EGARwFQ" />
      </Dialog>
      <AutoPlaySwipeableViews
        autoplay={open ? false : true}
        interval={6000}
        axis={theme.direction === "rtl" ? "x-reverse" : "x"}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
      >
        {tutorialSteps.map((step, index) => (
          <>
            <Card key={index}>
              <CardMedia className={classes.media} image={step.imgPath}>
                <Grid
                  style={{
                    height: "100%",
                    background:
                      "linear-gradient(180deg, rgba(0, 0, 0, 0) 30%, rgba(0, 0, 0, 1) 100%)",
                    // backgroundColor: "rgba(0, 0, 0, 0.5)",

                    color: "#fff",
                  }}
                  container
                  direction="row"
                  alignItems="center"
                >
                  <Grid style={{ marginLeft: 40 }} item xs={12}>
                    {/* <Grow in {...(true ? { timeout: 500 } : {})}>
                      <Typography
                        // style={{ backgroundColor: "#3CA162" }}
                        gutterBottom
                        variant="h5"
                      >
                        ХОВД ИХ СУРГУУЛЬ
                      </Typography>
                    </Grow> */}
                    <Grow in {...(true ? { timeout: 1000 } : {})}>
                      <Typography gutterBottom variant="h6">
                        {step.label}
                      </Typography>
                    </Grow>
                    <Grow in {...(true ? { timeout: 1000 } : {})}>
                      <Typography gutterBottom variant="h4">
                        {step.label2}
                      </Typography>
                    </Grow>

                    <Grow in {...(true ? { timeout: 1500 } : {})}>
                      {step.btnName !== "Танилцуулга" ? (
                        <Button
                          startIcon={<LanguageIcon />}
                          target="_blank"
                          href="http://elselt.khu.edu.mn/"
                          size="medium"
                          variant="contained"
                          style={{
                            color: "#fff",
                            backgroundColor: "#EA4335",
                            borderRadius: "40px",
                          }}
                        >
                          {step.btnName}
                        </Button>
                      ) : (
                        <>
                          <Grid style={{ padding: 10 }} item xs={12} md={4}>
                            {" "}
                            <Button
                              startIcon={<TouchAppIcon fontSize="large" />}
                              onClick={handleClickOpen}
                              size="medium"
                              variant="contained"
                              style={{
                                width: "190px",
                                color: "#fff",
                                backgroundColor: "#EA4335",
                                borderRadius: 0,
                                fontWeight: "bold",
                                borderRadius: "40px",
                              }}
                            >
                              Эрхэм зорилго
                            </Button>
                          </Grid>
                          <Grid style={{ padding: 10 }} item xs={12} md={4}>
                            {" "}
                            <Button
                              startIcon={
                                <PlayCircleOutlineIcon fontSize="large" />
                              }
                              onClick={handleClickOpen}
                              size="medium"
                              variant="contained"
                              style={{
                                width: "190px",

                                color: "#fff",
                                backgroundColor: "#007EFF",
                                borderRadius: 0,
                                fontWeight: "bold",
                                borderRadius: "40px",
                              }}
                            >
                              {step.btnName}
                            </Button>
                          </Grid>
                          <Grid style={{ padding: 10 }} item xs={12} md={4}>
                            {" "}
                            <Button
                              startIcon={<MailOutlineIcon fontSize="large" />}
                              onClick={handleClickOpen}
                              size="medium"
                              variant="contained"
                              style={{
                                width: "190px",
                                color: "#fff",
                                backgroundColor: "#FBBC05",
                                borderRadius: 0,
                                fontWeight: "bold",
                                borderRadius: "40px",
                              }}
                            >
                              Мэндчилгээ
                            </Button>{" "}
                          </Grid>{" "}
                        </>
                      )}
                    </Grow>
                  </Grid>
                </Grid>
              </CardMedia>
            </Card>
          </>
        ))}
      </AutoPlaySwipeableViews>
      {/* <MobileStepper
        steps={maxSteps}
        position="static"
        variant="dots"
        activeStep={activeStep}
        nextButton={
          <Button
            size="small"
            onClick={handleNext}
            disabled={activeStep === maxSteps - 1}
          >
            {theme.direction === "rtl" ? (
              <KeyboardArrowLeft />
            ) : (
              <KeyboardArrowRight />
            )}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === "rtl" ? (
              <KeyboardArrowRight />
            ) : (
              <KeyboardArrowLeft />
            )}
          </Button>
        }
      /> */}
    </div>
  );
};

export default MainSlide;
