import React, { useContext } from "react";
import MainSlide from "../Components/Sliders/MainSlide";
import MiniSlider from "../Components/Sliders/MiniSlider";

import NewsCTX from "../Context/NewsContext";
const Home = () => {
  const { AllNews, newsState } = useContext(NewsCTX);

  return (
    <div style={{ marginTop: "60px" }}>
      <MainSlide />
      <MiniSlider posts={newsState.posts} />
    </div>
  );
};

export default Home;
