import React, { useState, useEffect } from "react";
import axios from "../axios_Khu";
const NewsContext = React.createContext();

export const NewsStore = (props) => {
  useEffect(() => {
    AllNews();
  }, []);
  const [alert, setAlert] = useState({ value: false, type: "" });
  const [newsState, setNewsState] = useState({
    success: false,
    posts: [],
  });

  const AllNews = () => {
    axios
      .get(`posts`, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      })
      .then((res) => {
        const data = res.data.posts.reverse();
        setNewsState({ ...newsState, success: res.data.success, posts: data });
      })
      .catch((error) => console.log(error.response.data.error));
  };

  return (
    <NewsContext.Provider value={{ AllNews, newsState, setNewsState }}>
      {props.children}
    </NewsContext.Provider>
  );
};
export default NewsContext;
