import { ThemeProvider } from "@material-ui/core";
import React from "react";
import { BrowserRouter } from "react-router-dom";
import theme from "./theme/Theme";
import NavBar from "./Components/Navbar/NavBar.js";
import { makeStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Footer from "./Components/Footer/Footer";
import { Redirect, Route, Switch, useHistory } from "react-router";
import Scroll from "./Components/General/Scroll";
import Home from "./Pages/Home";
import About from "./Pages/About";

import { NewsStore } from "./Context/NewsContext";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: "60px",
    marginBottom: "50px",
    // minHeight: "100vh",
    // backgroundImage: `URL("https://images.unsplash.com/photo-1621888571553-569e36633e7b?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1834&q=80")`,
    // backgroundRepeat: "no-repeat",
    // backgroundSize: "cover",
  },
}));

const App = (props) => {
  const classes = useStyles();

  return (
    <>
      <ThemeProvider theme={theme}>
        <CssBaseline>
          <BrowserRouter>
            <NewsStore>
              {" "}
              <NavBar />
              <div className={classes.root}>
                <Switch>
                  <Route path="/home" exact component={Home} />
                  <Route path="/about" exact component={About} />
                  <Route path="/" exact component={Home} />
                  <Redirect from="*" to="/home" />
                </Switch>

                <Scroll showBelow={250} />
              </div>
              <Footer />
            </NewsStore>
          </BrowserRouter>
        </CssBaseline>
      </ThemeProvider>
    </>
  );
};

export default App;
